import React, { useState, useEffect } from "react";
import { Tabs } from "antd";
import axios from "axios";
//import Loader and Error from Bookingscreen.js
import Swal from "sweetalert2";
import { Tag, Divider } from "antd";
const { TabPane } = Tabs;

function Profilescreen() {
  const user = JSON.parse(localStorage.getItem("currentUser"));
  useEffect(() => {
    if (!user) {
      window.location.href = "/login";
    }
  }, []);
  return (
    <div className="mt-3 ml-3 mr-3 bs profile-box">
      <h1 className="text-center" style={{ fontSize: "30px" }}>
        <b>User Profile Panel</b>
      </h1>
      <Tabs defaultActiveKey="1">
        <TabPane tab="Profile" key="1">
          <div class=" card text-center" style={{ width: "18rem" }}>
            <img
              src="data:image/jpeg;base64,"
              class="card-img-top"
              alt="..."
            />
            <div class="card-body">
              <h5 class="card-title">
                <b>Name</b>: {user.name}
              </h5>
              <h5 class="card-title">
                <b>Email:</b> {user.email}
              </h5>
              <h5 class="card-title">
                <b>Is Admin?</b>: {user.isAdmin ? "Yes" : "No"}
              </h5>
            </div>
          </div>
        </TabPane>
        <TabPane tab="Bookings" key="2">
          <MyBookings />
        </TabPane>
      </Tabs>
    </div>
    // Add route in App.js
  );
}

export default Profilescreen;

// Bookings list component
export function MyBookings() {
  const [bookings, setbookings] = useState([]);
  const [review, setReview] = useState("");
  const [rating, setRating] = useState("");
  const [loading, setloading] = useState(false);
  const [error, seterror] = useState();
  const user = JSON.parse(localStorage.getItem("currentUser"));
  useEffect(() => {
    async function fetchdata() {
      try {
        setloading(true);
        const data = await (
          await axios.post("api/bookings/getbookingsbyuserid", {
            userid: user._id,
          })
        ).data;
        console.log(data);
        setbookings(data);
        setloading(false);
      } catch (error) {
        console.log(error);
        setloading(false);
        seterror(error);
      }
    }
    fetchdata();
  }, []);

  async function cancelbooking(bookingid, roomid) {
    try {
      setloading(true);
      const result = await (
        await axios.post("api/bookings/cancelbooking", { bookingid, roomid })
      ).data;
      console.log(result);
      setloading(false);
      Swal.fire(
        "Congratulations",
        "Your booking has been cancelled successfully",
        "success"
      ).then((result) => {
        window.location.reload();
      });
    } catch (error) {
      console.log(error);
      setloading(false);
      Swal.fire("Oops", "Something went wrong", "error");
    }
  }

  async function addreview(bookingid, roomid) {
    try {
      setloading(true);
      const result = await (
        await axios.post("api/bookings/getbookingsbyuserid/addreview", {
          bookingid,
          roomid,
          rating,
          review,
        })
      ).data;
      console.log(result);
      setloading(false);
      Swal.fire(
        "Congratulations",
        "Your review has been added successfully",
        "success"
      ).then((result) => {
        window.location.reload();
      });
    } catch (error) {
      console.log(error);
      setloading(false);
      Swal.fire("Oops", "Something went wrong", "error");
    }
  }

  // const review = (e) => {
  //   setText(e.target.value);
  // };

  // const rating = (e) => {
  //   setNumber(e.target.value);
  // };

  //
  return (
    <div className="row">
      <div className="col-md-12 m-2">
        <h1 className="text-center">Bookings</h1>
        {/* {loading && <Loader />} */}
        {bookings.length &&
          bookings.map((booking) => {
            return (
              <div className="bs booking-card">
                <h1>{booking.room}</h1>
                <p>
                  <b>Booking ID: {booking._id}</b>
                </p>
                <p>
                  <b>Check In: {booking.fromdate}</b>
                </p>
                <p>
                  <b>Check Out: {booking.todate}</b>
                </p>
                <p>
                  <b>Amount: {booking.totalamount}</b>
                </p>
                {booking.status === "cancelled" ? (
                  <Tag color="red">CANCELLED</Tag>
                ) : (
                  <Tag color="green">CONFIRMED</Tag>
                )}
                {booking.status !== "cancelled" && (
                  <div className="text-right mt-2">
                    {" "}
                    <button
                      className="btn btn-primary"
                      onClick={() => {
                        cancelbooking(booking._id, booking.roomid);
                      }}
                    >
                      CANCEL BOOKING
                    </button>{" "}
                    <div className="d-flex flex-column justify-content-center">
                      <div className="" style={{ fontSize: "24px" }}>
                        Rating:
                      </div>
                      <div style={{ width: "50px" }}>
                        <input
                          type="number"
                          name="rating"
                          value={rating}
                          onChange={(e) => {
                            setRating(e.target.value);
                          }}
                        />
                      </div>
                      <div style={{ fontSize: "24px" }}>Review:</div>
                      <textarea
                        rows="10"
                        cols="50"
                        onChange={(e) => {
                          setReview(e.target.value);
                        }}
                        value={review}
                      ></textarea>
                      <div className="row mt-3">
                        <div className="col-2 offset-1">
                          <button
                            className="btn btn-primary"
                            onClick={() => {
                              addreview(
                                booking._id,
                                booking.roomid,
                                rating,
                                review
                              );
                            }}
                          >
                            ADD REVIEW
                          </button>{" "}
                        </div>
                      </div>
                    </div>
                  </div>
                )}
              </div>
            );
          })}
        {/* index.css
            p{
                font-size: 16px !important;
            } */}
      </div>
      {/* Add the following code in bookingsRoute.js just above exports */}
      {/* router.post("/getbookingsbyuserid",async(req,res)=>{
                const userid=req.body.userid
                try {
                    const bookings=await Bookings.find({userid : userid})
                    res.send(bookings)
                } catch (error) {
                    return res.status(400).json({error});
                }
        }); */}
    </div>
  );
}
